import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'applicant'
})
export class ApplicantPipe implements PipeTransform {

  transform(value: string): string {
    return value === 'UL' ? ' ' : ' ';
  }

}

@Pipe({
  name: 'mode'
})
export class ModePipe implements PipeTransform {

  transform(value: string): string {
    return value === 'electric' ? ' ' : '';
  }

}

@Pipe({
  name: 'trademark'
})
export class TrademarkPipe implements PipeTransform {

  transform(value: string): string {
    if (value === 'word') {
      return '';
    } else if (value === 'image') {
      return '';
    } else {
      return '';
    }
  }

}

@Pipe({
  name: 'status'
})
export class StatusPipe implements PipeTransform {

  transform(value: string): string {
    switch (value) {
      case 'created':
        return '';
      case 'waiting':
        return '  ';
      case 'processing':
        return '  ';
      case 'checking':
        return '';
      case 'done':
        return '';
      case 'tax':
        return '  ';
      default:
        return value;
    }
  }

}

@Pipe({
  name: 'options'
})
export class OptionPipe implements PipeTransform {

  transform(value: string): string {
    switch (value) {
      case 'fullCheck':
        return '   ';
      case '':
        return '';
      default:
        return value;
    }
  }

}

@Pipe({
  name: 'doctypes'
})
export class DocTypesPipe implements PipeTransform {

  transform(value: string): string {
    switch (value) {
      case 'tax':
        return '  ';
      case 'proxy':
        return '';
      case 'permission':
        return '  ';
      default:
        return value;
    }
  }

}
